import {Component, OnInit} from '@angular/core';
import {IonicPage, NavController, NavParams, Platform} from 'ionic-angular';
import {VojPageHeaderType} from "../../../models/voj-page-header.model";
import {SearchHeaderData} from "../../../models/search-header.model";
import {VojCardModel} from "../../../models/voj-card.model";
import {SearchQuery} from "../../../models/search-query.model";
import {SearchServiceProvider} from "../../../providers/search-service/search-service";
import {EventBusProvider} from "../../../providers/voj-events/voj-events";
import {NavigationService} from "../../../services/navigation.service";
import {DetailsPage} from "../../details/details";
import {VojPage} from "../../../models/voj-page";
import {Configuration} from "../../../config/voj-configuration";
import {ModalService} from '../../../services/modal.service';
import {Geolocation} from '@ionic-native/geolocation';
import {InformationResumePage} from "../../information-resume/information-resume";
import {envConfig} from "../../../config/environment";
import {LoginPage} from "../../account-module/login/login";
import {Storage} from "@ionic/storage";

@IonicPage()
@Component({
    selector: 'page-search',
    templateUrl: 'search.html',
})
export class SearchPage implements OnInit {


    //  Page header
    pageHeader: VojPageHeaderType;
    searchHeaderData: SearchHeaderData;
    cards: Array<VojCardModel>;
    gmapView: boolean = false;
    sortType: string = 'pertinence';
    position: any;
    badge: number = 0;
    eventsCards: Array<VojCardModel> = [];
    eventsMode: boolean = false;
    emptyResults: string = "Aucun rsultat. Essayer avec la recherche multicritres";
    mobileStyle: any;
    messageStyle: any;

    constructor(private platform: Platform,
                public navCtrl: NavController,
                private storage: Storage,
                private geolocation: Geolocation,
                private eventBus: EventBusProvider,
                private navigationService: NavigationService, private configuration: Configuration,
                public searchService: SearchServiceProvider, private vojEvent: EventBusProvider,
                public modalService: ModalService, private navParams: NavParams) {

    }


    /**
     * This function patches the gmap synch issue
     * it is executed once a second after we switch from list to map view
     */
    checkSynchMap() {
        this.eventBus.publishMap(this.cards);

    }

    ngOnInit() {

        this.gmapView = false;
        if (!this.configuration.configuration.isLargeScreen) {
            this.mobileStyle = {
                "z-index": 1,
                "position": "relative",
                "background-color": "rgb(76, 84, 93)"
            };
            this.messageStyle = {
                'bottom': '5%',
                'position': 'absolute',
                'z-index': 1,
                'text-align': 'center',
                'width': '100%'
            }
        } else {
            this.mobileStyle = {};
            this.messageStyle = {
                'bottom': '30%',
                'position': 'fixed',
                'z-index': 1,
                'text-align': 'center',
                'width': '100%'
            }
        }

        /**
         *  This section is usually called before receiving
         */
        this.platform.ready().then(() => {
            this.gmapView = (this.platform.width() < envConfig.thresholdWidth);
            if (this.gmapView && this.pageHeader
                && this.pageHeader.actions && this.pageHeader.actions.length > 1) {
                this.pageHeader.actions[1].icon = 'list';
            }
        });

        if (this.navParams.data)
            if (this.navParams.data.isNewNotification) {
                console.log('badge : ' + this.navParams.data.app.badge);
                this.badge = this.navParams.data.app.badge;
            }

        //  Page header
        this.pageHeader = new VojPageHeaderType();
        this.pageHeader.title = "Recherche";
        this.pageHeader.isCanceled = false;
        this.pageHeader.actions = [
            {
                id: 'notification',
                icon: '',
                label: String(this.badge),
                action: () => {
                    setTimeout(() => {
                        this.vojEvent.publishPushEvents();
                    }, 1000);
                }
            },
            {
                id: 'map',
                icon: 'pin',
                action: () => {
                    if (!this.configuration.configuration.isLargeScreen) {
                        this.gmapView = !this.gmapView;
                        this.pageHeader.actions[1].icon = this.gmapView ? 'list' : 'pin';
                        if (this.cards && this.gmapView) {
                            setTimeout(() => {
                                this.checkSynchMap();
                            }, 1000);

                        }
                        //this.event.publish('MAP_DATA', this.cards);
                    } else {
                        this.gmapView = false;
                    }

                },
                label: ''
            }
        ];

        this.searchHeaderData = {
            placeholders: {
                main: 'Recherche libre',
                what: 'Quoi ?',
                where: 'O ?',
                when: 'Quand ?',
                who: 'Qui ?'
            },

            action: (q: SearchQuery) => {
                this.search(q);
            }
        };
        this.geolocation.getCurrentPosition().then((resp) => {
            this.position = resp;

            //  Let us retrive the most recent results now that we know where we are
            this.searchService.retrieveRecent().then((data: any) => {
                this.sortType = 'pertinence';
                this.cards = data;
                for (let i = 0; i < this.cards.length; i++) {
                    let c = this.cards[i];
                    c.scoreOrder = i;
                    for (let j = 0; j < c.actions.length; j++) {
                        let action = c.actions[j];
                        if (action.id == "details") {
                            action.action = () => {
                                this.gotoDetails(c);
                            };
                            break;
                        }
                    }
                }
                //TODO TIM: an event service that defines an event for every data structure and then verify types of sent objects
                //TODO TIM: All maps will be updated. We should define witch map to update, if we have more than one..
                // sending values to map component in other pages
                /*let mapPassenger: eBusType = this.eventBus.passenger.type.get('map');
                 mapPassenger.data = this.cards;
                 this.eventBus.set(mapPassenger);*/
                this.eventBus.publishMap(this.cards);

                if (this.position && this.position.coords)
                    this.saveProximityOrder(this.position.coords.latitude, this.position.coords.longitude);
            });

        }).catch((data: any) => {

        });
        this.eventBus.subscribeSearch(this);


    }

    delete(chip: Element) {
        chip.remove();
    }


    updateCards(cards: Array<VojCardModel>) {
        if (cards && cards.length > 0) {
            this.badge = cards.length;
            this.eventsCards = cards;
        }

    }

    loadEvents() {
        this.eventsMode = true;
        this.searchService.eventMode = true;
        this.cards = this.eventsCards;
        for (let i = 0; i < this.cards.length; i++) {
            let c = this.cards[i];
            c.scoreOrder = i;
            for (let j = 0; j < c.actions.length; j++) {
                let action = c.actions[j];
                if (action.id == "details") {
                    action.action = () => {
                        this.gotoDetails(c);
                    };
                    break;
                }
            }
        }
        //TODO TIM: an event service that defines an event for every data structure and then verify types of sent objects
        //TODO TIM: All maps will be updated. We should define witch map to update, if we have more than one..
        this.eventBus.publishMap(this.cards);
        if (this.position && this.position.coords)
            this.saveProximityOrder(this.position.coords.latitude, this.position.coords.longitude);
    }

    search(q: SearchQuery) {
        this.eventsMode = false;
        if (q.queryType == 'full_text' && q.freeText && q.freeText != '') {

            //TODO TIM: Data should have an Array<VojCardType> as type
            //TODO ABDESLAM: If data is of VojCardType type it will be too generic and unusable for other apps and services
            this.searchService.semanticSearch(q.freeText).then((data: any) => {
                this.sortType = 'pertinence';
                this.cards = data;
                for (let i = 0; i < this.cards.length; i++) {
                    let c = this.cards[i];
                    c.scoreOrder = i;
                    for (let j = 0; j < c.actions.length; j++) {
                        let action = c.actions[j];
                        if (action.id == "details") {
                            action.action = () => {
                                this.gotoDetails(c);
                            };
                            break;
                        }
                    }
                }
                //TODO TIM: an event service that defines an event for every data structure and then verify types of sent objects
                //TODO TIM: All maps will be updated. We should define witch map to update, if we have more than one..
                // sending values to map component in other pages
                /*let mapPassenger: eBusType = this.eventBus.passenger.type.get('map');
                mapPassenger.data = this.cards;
                this.eventBus.set(mapPassenger);*/
                this.eventBus.publishMap(this.cards);

                if (this.position && this.position.coords)
                    this.saveProximityOrder(this.position.coords.latitude, this.position.coords.longitude);
            });

        } else if (q.queryType == 'criteria' && (q.job || q.location || q.timeslot || q.name)) {
            this.searchService.criteriaSearch(q).then((data: any) => {
                this.sortType = 'pertinence';
                this.cards = data;
                for (let i = 0; i < this.cards.length; i++) {
                    let c = this.cards[i];
                    c.scoreOrder = i;
                    for (let j = 0; j < c.actions.length; j++) {
                        let action = c.actions[j];
                        if (action.id == "details") {
                            action.action = () => {
                                this.gotoDetails(c);
                            };
                            break;
                        }
                    }
                }
                /*let mapPassenger: eBusType = this.eventBus.passenger.type.get('map');
                mapPassenger.data = this.cards;
                this.eventBus.set(mapPassenger);*/
                this.eventBus.publishMap(this.cards);

                if (this.position && this.position.coords)
                    this.saveProximityOrder(this.position.coords.latitude, this.position.coords.longitude);
            });
        }
    }

    saveProximityOrder(x0: number, y0: number) {
        for (let i = 0; i < this.cards.length; i++) {
            if (!this.cards[i].address.lat || !this.cards[i].address.lng)
                this.cards[i].locationOrder = 9999; // large distance to ensure sending the card to the end

            let x1 = this.cards[i].address.lat;
            let y1 = this.cards[i].address.lng;

            let distance = Math.sqrt((x0 - x1) * (x0 - x1) + (y0 - y1) * (y0 - y1));  // Thanks Pythagoras
            this.cards[i].locationOrder = distance;
        }
    }

    doSort(e) {
        if (this.sortType == 'pertinence') {
            this.cards.sort((a: VojCardModel, b: VojCardModel) => {
                return a.scoreOrder - b.scoreOrder;
            });
        } else {
            this.cards.sort((a: VojCardModel, b: VojCardModel) => {
                return a.locationOrder - b.locationOrder;
            });
        }
    }

    gotoDetails(card: VojCardModel) {

        //  Check if user is authenticated
        this.storage.get("currentUser").then((sdata: any) => {
            let data = JSON.parse(sdata);
            if (data && data.account) {

                //  OK user is authenticated
                let c: VojCardModel = null;
                let results = this.eventsMode ? this.searchService.events : this.searchService.results;
                for (let i = 0; i < results.length; i++) {
                    if (results[i].id == card.id) {
                        c = results[i].toCardDetails();
                        break;
                    }
                }

                if (c == null) {
                    //  bullocks :(
                    return;
                }

                let vojNav: VojPage = {page: DetailsPage, params: {card: c}, isRoot: false};
                this.navigationService.navigate('details', vojNav);
            } else {
                //  These are not the droids you are looking for
                let vojPageList: VojPage = {page: LoginPage, isRoot: true, isModal: true};
                this.navigationService.navigate("list", vojPageList);
            }
        });


    }

    goToInformationResumePage() {
        let vojNav: VojPage = {page: InformationResumePage, isRoot: false};
        this.navigationService.navigate('details', vojNav);
    }

    /*
    goToSignUp() {
        this.modalService.present(SignUpPage);

    }

    presentResetPassword() {
        console.log('Call Reset Password.');
        // this.resetPassword.present();
        this.modalService.present(ResetPasswordPage, false);
    }

    goToJobsListPage() {
        let vojNav: VojPage = {page: LanguagesPage, isRoot: false};
        this.navigationService.navigate('details', vojNav);
    }
    */
    ionViewDidLoad() {
        console.log('ionViewDidLoad SearchPage');
    }

}



